import React from 'react';
import { bool, func } from 'prop-types';
import { StyledMenu } from './menustyle';
import { NavLink } from "react-router-dom";

const MenuList = ({ open, setOpen }) => {
  return (
    <StyledMenu open={open} onClick={() => setOpen(!open)}>
      <NavLink exact activeClassName = "active" to = "/" >HOME</NavLink>
      <NavLink activeClassName = "active" to = "/Menu" >MENU</NavLink>
      <NavLink activeClassName = "active" to = "/Stylist" >STYLIST</NavLink>
      <NavLink activeClassName = "active" to = "/Reserve" >RESERVE</NavLink>
      <NavLink activeClassName = "active" to = "/Access">ACCESS</NavLink>
      <NavLink activeClassName = "active" to = "/About" >ABOUT</NavLink>
      <NavLink activeClassName = "active" to = "/Reqruit" >RECRUIT</NavLink>
    </StyledMenu>
  )
}

MenuList.propTypes = {
  open: bool.isRequired,
  setOpen: func.isRequired,
}

export default MenuList;
