import React, { Component } from "react";
import Slider from "react-slick";
import Pic1 from "../../images/home1.jpg";
import Pic2 from "../../images/home2.jpg";
import Pic3 from "../../images/home3.jpg";

export default class FadeSlide extends Component {
  render() {
    const settings = {
      arrows: false,
      dots: true,
      fade: true,
      infinite: true,
      speed: 4000,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 10000,
    };
    return (
      <div className="slide-container">
        <Slider {...settings}>
          <div>
            <img className="slide-pic" src={Pic1} alt="pic1" />
          </div>
          <div>
            <img className="slide-pic" src={Pic2} alt="pic2" />
          </div>
          <div>
            <img className="slide-pic" src={Pic3} alt="pic3" />
          </div>
        </Slider>
      </div>
    );
  }
}
